<template>
  <div class="safe">
    <van-nav-bar title="安全中心" left-text="返回" left-arrow :fixed="true" @click-left="back" />
    <div class="secure-bg">
    </div>
    <div class="secure-box">
      <div>
        <van-cell title="修改密码" @click="openpasswordpopup">
          <div class="an">
            <img src="../assets/images/xiugai.png" alt class="auto-img tuichu" />
          </div>
        </van-cell>

        <van-cell title="退出登录" @click="outlogin">
          <div class="an">
            <img src="../assets/images/tui.png" alt class="auto-img tuichu" />
          </div>
        </van-cell>

        <van-cell title="忘记密码" @click="forget">
          <div class="an">
            <img src="../assets/images/forget.png" alt class="auto-img tuichu" />
          </div>
        </van-cell>

        <van-cell title="注销账号" @click="cancelpassword">
          <div class="an">
            <img src="../assets/images/zhuxiao.png" alt class="auto-img tuichu" />
          </div>
        </van-cell>
      </div>
    </div>

    <van-popup class="popup-box" closeable v-model="isOpen" position="bottom">
      <div class="popup-item">
        <van-divider :style="{ color: '#5f56ff', borderColor: '#5f56ff' }">
          <div class="password-text">修改密码</div>
        </van-divider>
        <van-form>
          <van-field
            v-for="(item, index) in passwords"
            :key="index"
            :type="item.isPassword ? 'password' : 'text'"
            :label="item.title"
            placeholder="6-16位密码且以字母开头"
            v-model="item.password"
            :right-icon="item.isPassword ? 'closed-eye' : 'eye-o'"
            @click-right-icon="lookpassword(item)"
          />
          <div class="login-btn">
            <van-button
              round
              block
              color="#5f56ff"
              native-type="submit"
              @click="confirmPassword"
            >确认修改</van-button>
          </div>
        </van-form>
      </div>
    </van-popup>
  </div>
</template>

<script>
import "../assets/less/safe.less";

import { utils } from "../assets/js/utils";

import { validForm } from "../assets/js/validForm";

export default {
  data() {
    return {
      isOpen: false,
      passwords: {
        old: {
          password: "",
          isPassword: true,
          title: "旧密码",
        },
        new: {
          password: "",
          isPassword: true,
          title: "新密码",
        },
      },
    };
  },
  methods: {
    back() {
      this.$router.go(-1);
    },

    //查看密码,文本密码类型切换
    lookpassword(item) {
      item.isPassword = !item.isPassword;
    },

    //退出登录
    outlogin() {
      this.$dialog
        .confirm({
          title: "退出登录",
          message: "是否确认退出登录！",
          confirmButtonColor: "#5f56ff",
        })
        .then(() => {
          this.$toast.loading({
            message: "加载中...",
            forbidClick: true,
            duration: 0,
            loadingType: "spinner",
          });

          localStorage.removeItem("CSTK");

          setTimeout(() => {
            this.$toast.clear();
            this.$router.push({ name: "Login" });
          }, 1000);
        })
        .catch(() => {
          this.$notify({ background: '#5f56ff', message: '取消退出登录' })
        });
    },

    //修改密码
    openpasswordpopup() {
      this.isOpen = true;
    },


    //确认修改密码
    confirmPassword() {
      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      let o = {
        oldPassword: {
          value: this.passwords.old.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "旧密码支持字母数字_组合",
        },
        password: {
          value: this.passwords.new.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "新密码支持字母数字_组合",
        },
      };

      //验证表单
      if (!validForm.valid(o)) {
        //验证不通过
        return;
      }

      //发起修改密码请求
      //验证通过
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      //参数序列化
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        oldPassword: this.passwords.old.password,
        password: this.passwords.new.password,
      });

      this.axios({
        method: "POST",
        url: "/updatePassword",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          
          if (result.data.code == "E001") {
            localStorage.removeItem("CSTK");
            this.$router.push({ name: "Login" });
          } else {
            this.$notify({
              type: "warning",
              message: result.data.msg,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    //注销账号
    cancelpassword() {
      this.$dialog
        .confirm({
          title: "注销账号",
          message: "是否确认注销号，一旦确认无法恢复！",
          confirmButtonColor: "#5f56ff",
        })
        .then(() => {
          
          //获取token字符串
          let tokenString = localStorage.getItem("CSTK");
          if (!tokenString) {
            return this.$router.push({ name: "Login" });
          }

          this.$toast.loading({
            message: "加载中...",
            forbidClick: true,
            duration: 0,
            loadingType: "spinner",
          });

          //参数序列化
          let data = utils.queryString({
            appkey: this.appkey,
            tokenString,
          });

          this.axios({
            method: "POST",
            url: "/destroyAccount",
            data,
          })
            .then((result) => {
              this.$toast.clear();
              
              this.$toast(result.data.msg);
              if (result.data.code == "G001") {
                localStorage.removeItem("CSTK");
                setTimeout(() => {
                  this.$router.push({ name: "Login" });
                }, 600);
              }
            })
            .catch((err) => {
              this.$toast.clear();
              
            });
        })
        .catch(() => {
          
        });
    },

    //忘记密码
    forget() {
      this.$router.push({ name: "Forget" });
    },
  },
};
</script>

<style lang="less" scoped>
</style>